  computed: {
    ...mapGetters(['getMortuaryList', 'getEnterRoomList', 'getObituaryDetail', 'memberInfo','getSetting','getReligionList']),
    casketIngTimeString() {
      let content = ''
      for (let i = 0; i < this.getEnterRoomList.length; i++) {
        if (this.getEnterRoomList[i].id === this.obituary.obituaryEnterRoom.id) {
          for (let j = 0; j < this.getEnterRoomList[i].enterRoomObituary.length; j++) {
            const enterRoomObituary = this.getEnterRoomList[i].enterRoomObituary[j]
            content += '이용중인 입관 시간 : ' + enterRoomObituary.obituaryName + '(' + (enterRoomObituary.obituaryMortuary !== null && enterRoomObituary.obituaryMortuary !== undefined ? enterRoomObituary.obituaryMortuary.mortuaryNumber : '') + ') ' + this.YYMMDDHHmmDate(enterRoomObituary.obituaryCasketDate) + '<br>'
          }
          return content;
        }
      }
      return content
    },
    chageKey() {
      return this.getObituaryDetail.id
    },
  },
  mounted() {
    $(".selector").flatpickr({
      dateFormat: "Y-m-d",
      
		  "locale": "ko",
      maxDate: new Date().fp_incr(30),
      disableMobile: "true"
    })
    this.$store.dispatch('REQUEST_OBITUARY')
    const parameter = {
      id: this.id
    }
    this.$store.dispatch('REQUEST_OBITUARY_DETAIL', parameter).then(data => {
      if (data) {
        let obituaryDetailCopy = JSON.parse(JSON.stringify(this.getObituaryDetail))
        this.obituary = obituaryDetailCopy
        this.url = this.obituary.obituaryFile !== null && this.obituary.obituaryFile !== undefined && this.obituary.obituaryFile.length > 0 ? this.obituary.obituaryFile[0].filePath : null;
        this.obituary.obituaryMember = null
        if (this.memberInfo.memberAttr === 'TYPE-0') {
          if (obituaryDetailCopy.obituaryMortuary !== null && obituaryDetailCopy.obituaryMortuary !== undefined) {
            this.obituary.obituaryMortuary = {
              id: obituaryDetailCopy.obituaryMortuary.id
            }
            this.mortuaryInFlag = 'in'
          } else {
            this.obituary.obituaryMortuary = null
            this.mortuaryInFlag = 'out'
          }
        } else {
          this.obituary.obituaryMortuary = null
          if (obituaryDetailCopy.obituaryMortuaryName !== null && obituaryDetailCopy.obituaryMortuaryName !== undefined && obituaryDetailCopy.obituaryMortuaryName !== '') {
            this.obituary.obituaryMortuaryName = obituaryDetailCopy.obituaryMortuaryName
            this.mortuaryInFlag = 'in'
          } else {
            this.obituary.obituaryMortuaryName = null
            this.mortuaryInFlag = 'out'
          }
        }
        if (obituaryDetailCopy.obituaryDieDate !== null && obituaryDetailCopy.obituaryDieDate !== undefined) {
          let dieDate = obituaryDetailCopy.obituaryDieDate.split(' ');
          this.obituary.obituaryDieDate = dieDate[0]
        }
        if (obituaryDetailCopy.obituaryEnterDate !== null && obituaryDetailCopy.obituaryEnterDate !== undefined) {
          let enterDate = obituaryDetailCopy.obituaryEnterDate.split(' ');
          this.enterDateString = enterDate[0];
          this.enterTimeString = enterDate[1].substring(0, 5);
          let enterTimeStringSplit = this.enterTimeString.split(':');
          this.enterTimeString1 = enterTimeStringSplit[0]
          this.enterTimeString2 = enterTimeStringSplit[1]
        }
        if (this.memberInfo.memberAttr === 'TYPE-0') {
          if (obituaryDetailCopy.obituaryEnterRoom !== null && obituaryDetailCopy.obituaryEnterRoom !== undefined) {
            this.obituary.obituaryEnterRoom = {
              id: obituaryDetailCopy.obituaryEnterRoom.id
            }
          } else {
            this.obituary.obituaryEnterRoom = { id: '' }
          }
        } else {
          this.obituary.obituaryEnterRoom = { id: '' }
        }
        if (obituaryDetailCopy.obituaryCasketDate !== null && obituaryDetailCopy.obituaryCasketDate !== undefined) {
          let casketDate = obituaryDetailCopy.obituaryCasketDate.split(' ');
          this.casketDateString = casketDate[0];
          this.casketTimeString = casketDate[1].substring(0, 5);
          let casketTimeStringSplit = this.casketTimeString.split(':');
          this.casketTimeString1 = casketTimeStringSplit[0]
          this.casketTimeString2 = casketTimeStringSplit[1]
        }
        if (obituaryDetailCopy.obituaryReligionDate !== null && obituaryDetailCopy.obituaryReligionDate !== undefined) {
          let religionDate = obituaryDetailCopy.obituaryReligionDate.split(' ');
          this.religionDateString = religionDate[0];
          this.religionTimeString = religionDate[1].substring(0, 5);
          let religionTimeStringSplit = this.religionTimeString.split(':');
          this.religionTimeString1 = religionTimeStringSplit[0]
          this.religionTimeString2 = religionTimeStringSplit[1]
        }
        if (obituaryDetailCopy.obituaryEndDate !== null && obituaryDetailCopy.obituaryEndDate !== undefined) {
          let endDate = obituaryDetailCopy.obituaryEndDate.split(' ');
          this.endDateString = endDate[0];
          this.endTimeString = endDate[1].substring(0, 5);
          let endTimeStringSplit = this.endTimeString.split(':');
          this.endTimeString1 = endTimeStringSplit[0]
          this.endTimeString2 = endTimeStringSplit[1]
          this.endTimeVisibleCheck = false
        } else {
          this.endTimeVisibleCheck = true
        }
        if (obituaryDetailCopy.obituaryObituaryMember !== null && obituaryDetailCopy.obituaryObituaryMember !== undefined) {
          this.obituaryMemberList = obituaryDetailCopy.obituaryObituaryMember
          for(let k=0;k<this.obituaryMemberList.length;k++){
							this.obituaryMemberList[k].success = true
						}
        }
        this.obituary.obituaryAddrName = obituaryDetailCopy.obituaryAddrName
        this.obituary.obituaryAddr = obituaryDetailCopy.obituaryAddr
        this.obituary.obituaryAddrX = obituaryDetailCopy.obituaryAddrX
        this.obituary.obituaryAddrY = obituaryDetailCopy.obituaryAddrY
        this.obituary.obituaryMortuaryName = obituaryDetailCopy.obituaryMortuaryName
        this.obituary.post = obituaryDetailCopy.post
        if (this.memberInfo.memberAttr === 'TYPE-1') {
          this.obituary.obituaryMortuary = null
        }

        
					
					if(this.obituary.obituaryMusicYn){
						this.obituary.obituaryMusicYn = 'true'
						this.musicName = this.obituary.obituaryMusic.musicFile[0].fileName
					}else{
						this.obituary.obituaryMusicYn = 'false'
          }
          
					if(this.obituary.obituaryPictureMusicYn){
						this.obituary.obituaryPictureMusicYn = 'true'
						this.musicPcitureName = this.obituary.obituaryPictureMusic.musicFile[0].fileName
					}else{
						this.obituary.obituaryPictureMusicYn = 'false'
					}

          
					if(this.obituary.obituaryPlace === ''){
						this.placeVisibleCheck = true
					}else{
						this.placeVisibleCheck = false
					}

      }
    })
    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', '@/assets/js/ux_common.js')
    //document.head.appendChild(recaptchaScript)
  },
  beforeDestroy() {
    this.$store.commit('OBITUARY_INIT')
  },
  methods: {
		changeDate(){
      if(this.endDateString!==null && this.endDateString !==undefined && this.endDateString!==''){
        return
      }
			let obituaryDieDate = new Date(this.obituary.obituaryDieDate)
			
			obituaryDieDate.setDate(obituaryDieDate.getDate()+2)
			//this.endDateString = this.YYMMDDSLASHDate(obituaryDieDate)
		},
		accountCertifyInit(index){
			
			this.obituaryMemberList[index].success = false
		},
		accountCertify(obituaryMember){
			const parameter = {
				accountNumber: obituaryMember.obituaryMemberBankNumber,
				bankCode: obituaryMember.obituaryMemberBank
			}
			this.$store.dispatch('REQUEST_ACCOUNT_CERTIFY',parameter).then(data => {
				
				if (data.result) {
					if(obituaryMember.obituaryMemberName.indexOf(data.name)>-1){
						alert("계좌가 확인되었습니다.")
						obituaryMember.success = true
					}else{
						obituaryMember.success = false
						alert('성함이 일치하지 않습니다.')
					}
					
				}
			})

		},
    moveBack() {
      this.$router.go(-1)
    },

    
		openMusic(music){
			
			this.musicFlag = !this.musicFlag
			if(this.musicFlag){
				this.$store.commit('START_DEEMED');
			}else{
				if(music!==null && music !== undefined){
					
					this.obituary.obituaryMusic = {id:music.id}
					this.musicName = music.musicFile[0].fileName
				}
				this.$store.commit('END_DEEMED');
			}
		},
		openPictureMusic(music){
			this.musicPictureFlag = !this.musicPictureFlag
			if(this.musicPictureFlag){
				this.$store.commit('START_DEEMED');
			}else{
				if(music!==null && music !== undefined){
					
					this.obituary.obituaryPictureMusic = {id:music.id}
					this.musicPcitureName = music.musicFile[0].fileName
				}
				this.$store.commit('END_DEEMED');
			}
		},

    onlyNumberAge() {
      this.obituary.obituaryAge = this.obituary.obituaryAge.replace(/[^0-9.]/g, '');
    },
    onlyNumberPhone(index) {
      this.obituaryMemberList[index].obituaryMemberPhone = this.obituaryMemberList[index].obituaryMemberPhone.replace(/[^0-9.]/g, '');
    },
    inputEnterTime($event) {
      this.enterTimeString = this.changeHHMM($event.target.value)
    },
    inputCasketTime($event) {
      this.casketTimeString = this.changeHHMM($event.target.value)
    },
    inputReligionTime($event) {
      this.religionTimeString = this.changeHHMM($event.target.value)
    },
 
		placeCheck(event){

			this.placeVisibleCheck = event.target.checked
			if(this.placeVisibleCheck){
				this.obituary.obituaryPlace=""
			}
		},
		placeChange(){
			
			if(this.obituary.obituaryPlace!==""){
				this.placeVisibleCheck = false
			}
		},
		endTimeCheck(event){
			this.endTimeVisibleCheck = event.target.checked
			if(this.endTimeVisibleCheck){
				this.endDateString = ""
				this.endTimeString1 = null
				this.endTimeString2 = null
			}
		},
		endCasketCheck(event){

			this.endCasketVisibleCheck = event.target.checked
			if(this.endCasketVisibleCheck){
				this.casketDateString = ""
				this.casketTimeString1 = null
				this.casketTimeString2 = null
			}
		},

		endReligionCheck(event){

			this.endReligionVisibleCheck = event.target.checked
			if(this.endReligionVisibleCheck){
				this.religionDateString = ""
				this.religionTimeString1 = null
				this.religionTimeString2 = null
			}
		},
		endDateChange($event){
			
			if((this.endDateString !== null && this.endDateString !== "")  || (this.endTimeString1 !==null && this.endTimeString1 !=="")  || (this.endTimeString2!==null && this.endTimeString2!=="")){
				
				this.endTimeVisibleCheck = false
			}else{
				this.endTimeVisibleCheck = true
			}
		},
		endTimeChange($event) {
			if (this.endTimeVisibleCheck) {
				if (this.endDateString === "" || this.endTimeString === "") {
					this.endTimeVisibleCheck = false
				}
			}
			this.endTimeString = this.changeHHMM($event.target.value)
		},
    addObituaryMember() {
      this.obituaryMemberList.push({
        id: null,
        obituaryMemberObituary: {
          id: null
        },
        obituaryMemberName: "",
        obituaryMemberType: "",
        obituaryMemberPhone: "",
        obituaryMemberLeader: false,
        obituaryMemberBank: "",
        obituaryMemberBankNumber: ""
      })
    },
    deleteObituaryMember(index) {
      this.obituaryMemberList.splice(index, 1)
    },
    leaderChange(event, index) {
      let obituaryMemberListCopy = JSON.parse(JSON.stringify(this.obituaryMemberList))
      for (let i = 0; i < obituaryMemberListCopy.length; i++) {
        if (i === index) {
          obituaryMemberListCopy[i].obituaryMemberLeader = true
        } else {
          obituaryMemberListCopy[i].obituaryMemberLeader = false
        }
      }
      this.obituaryMemberList = obituaryMemberListCopy
    },
    obituaryNext() {
			let i= this.obituaryMemberList.length
			for(i ; i !== 0;i--){
				if(this.obituaryMemberList[i-1].obituaryMemberName === '' ){

					this.obituaryMemberList.splice(i-1, 1)
				}
			}
      this.obituaryRegOrder = 'second'
    },
    obituaryBefore() {
      this.obituaryRegOrder = 'first'
    },
    obituaryReg() {
      if (this.enterTimeString1 != null && this.enterTimeString1 !== '' && this.enterTimeString2 !== null && this.enterTimeString2 !== '') {
        this.enterTimeString = this.enterTimeString1 + ":" + this.enterTimeString2
      }
      if (this.casketTimeString1 != null && this.casketTimeString1 !== '' && this.casketTimeString2 !== null && this.casketTimeString2 !== '') {
        this.casketTimeString = this.casketTimeString1 + ":" + this.casketTimeString2
      }
      if (this.religionTimeString1 != null && this.religionTimeString1 !== '' && this.religionTimeString2 !== null && this.religionTimeString2 !== '') {
        this.religionTimeString = this.religionTimeString1 + ":" + this.religionTimeString2
      }
      if (this.endTimeString1 != null && this.endTimeString1 !== '' && this.endTimeString2 !== null && this.endTimeString2 !== '') {
        this.endTimeString = this.endTimeString1 + ":" + this.endTimeString2
      }
      if (!this.validationCheck()) {
        return
      }
      
      let obituaryCopy = JSON.parse(JSON.stringify(this.obituary))
      
			if(obituaryCopy.obituaryAge===null || obituaryCopy.obituaryAge === ''){
				obituaryCopy.obituaryAge=null
				obituaryCopy.obituaryAgeYn = true
			}
			if(obituaryCopy.obituarySex===null || obituaryCopy.obituarySex === ''){
				obituaryCopy.obituarySexYn = true
				obituaryCopy.obituarySex=null
			}


			if(obituaryCopy.obituaryDieDate!==null && obituaryCopy.obituaryDieDate!==undefined && obituaryCopy.obituaryDieDate!==''){
				obituaryCopy.obituaryDieDate = obituaryCopy.obituaryDieDate + ' 00:00:00'
			}
      if (this.enterDateString !== null && this.enterDateString !== '' && this.enterTimeString !== null && this.enterTimeString !== '') {
        obituaryCopy.obituaryEnterDate = this.enterDateString + ' ' + this.enterTimeString + ':00'
      } else {
        obituaryCopy.obituaryEnterDate = null
      }

      if (this.casketDateString !== null && this.casketDateString !== '' && this.casketTimeString !== null && this.casketTimeString !== '') {
        obituaryCopy.obituaryCasketDate = this.casketDateString + ' ' + this.casketTimeString + ':00'
      } else {
        obituaryCopy.obituaryCasketDate = null
      }

      if (this.religionDateString !== null && this.religionDateString !== '' && this.religionTimeString !== null && this.religionTimeString !== '') {
        obituaryCopy.obituaryReligionDate = this.religionDateString + ' ' + this.religionTimeString + ':00'
      } else {
        obituaryCopy.obituaryReligionDate = null
      }

      if (this.endDateString !== null && this.endDateString !== '' && this.endTimeString !== null && this.endTimeString !== '') {
        obituaryCopy.obituaryEndDate = this.endDateString + ' ' + this.endTimeString + ':00'
      } else {
        obituaryCopy.obituaryEndDate = null
      }
      obituaryCopy.obituaryObituaryMember = this.obituaryMemberList
      if (obituaryCopy.obituaryEnterRoom.id === '') {
        obituaryCopy.obituaryEnterRoom = null
      }

      
			this.$store.commit('START_DEEMED');
            this.agreeFlag =true

			this.comfirmobituaryCopy =obituaryCopy
			this.comfirmfile = this.files
			
      
    },

    submit(val){
			if(!val){
				alert("개인정보 동의를 진행해 주세요.")
				return;
			}
            this.agreeFlag = false
			this.$store.commit('END_DEEMED');
			const parameter = {
				obituary:this.comfirmobituaryCopy,
				files:this.comfirmfile
			}
			this.$store.dispatch("REQUEST_OBITUARY_REG", parameter)
        },
        async changeFiles(event){
			const context =this
			if(event.target.files.length>0){
				
				//this.imageResize(event.target.files[0])

				let file = event.target.files[0];	// 입력받은 file객체
					
					// 이미지 resize 옵션 설정 (최대 width을 100px로 지정)
					const options = { 
						maxSizeMB: 0.2,
						maxWidthOrHeight: 1300
					}
					
					try {
						
						const compressedFile = await imageCompression(file, options);
						//setFile(compressedFile);
						
						const fileParam = []
						fileParam.push(compressedFile)
						context.canvasFlag = true

						context.selectedFiles = fileParam
					} catch (error) {
						console.log(error);
					}

			}
		},
		cropedFiles(files,displayType){
      this.files = files
      
			  this.obituary.obituaryFile=[]
       this.url = URL.createObjectURL(this.files[0]);
			 this.obituary.obituaryDisplayType = displayType
            this.canvasFlag = false
		},
		reFileCrop(){
			
			if(this.selectedFiles.length>0 && this.files.length>0){
				if(this.selectedFiles===null || this.selectedFiles=== undefined || this.selectedFiles.length===0){

					this.selectedFiles =this.files
					this.canvasFlag = true
				}else{
					
					this.canvasFlag = true
				}
			}
		},
		imagePopupClose(){
			
				this.canvasFlag = false

		},
    fileUpload(){
      this.$refs.fileupload.click()
		},
		
