				}

				if (this.religionTimeString2 === null || this.religionTimeString2 === ''){

					alert("종교 행사시간을 입력해주세요 ")
					return
				}
			}
      
      if (this.memberInfo.memberAttr === 'TYPE-1') {
        if (this.obituary.obituaryAddrName === null || this.obituary.obituaryAddrName === undefined || this.obituary.obituaryAddrName === '') {
          alert('장례식장 명을 입력해 주세요.')
