        }
      }

      
			if(this.obituary.obituaryMusicYn === 'true'){
				if(this.obituary.obituaryMusic===null || this.obituary.obituaryMusic.id === null || this.obituary.obituaryMusic.id === undefined || this.obituary.obituaryMusic.id===''){
					alert('음악을 선택해 주세요.');
					return false
				}
			}else{
				this.obituary.obituaryMusic = null
      }
      
			if(this.obituary.obituaryPictureMusicYn === 'true'){
