			if(!this.endTimeVisibleCheck && (this.endDateString===undefined || this.endDateString===null || this.endDateString === '' || this.endTimeString1===undefined  || this.endTimeString1===null || this.endTimeString1 === ''
				|| this.endTimeString2===undefined  || this.endTimeString2===null || this.endTimeString2 === '')){
					alert('발인일을 입력해 주세요.')
					return false
			}
			if(!this.placeVisibleCheck && (this.obituary.obituaryPlace==='')){
				alert('장지를 입력해 주세요.')
					return false
			}


      for(let i=0;i<this.obituaryMemberList.length;i++){
					if(this.obituaryMemberList[i].obituaryMemberType===null || this.obituaryMemberList[i].obituaryMemberType===undefined || this.obituaryMemberList[i].obituaryMemberType===''){
						alert('고인과의 관계를 입력해 주세요.')
						return false
					}
			}

			for(let i=0;i<this.obituaryMemberList.length;i++){
					if(this.obituaryMemberList[i].obituaryMemberName===null || this.obituaryMemberList[i].obituaryMemberName===undefined || this.obituaryMemberList[i].obituaryMemberName===''){
						alert('상주명을 입력해 주세요.')
						return false
					}
      }
      
      for (let i = 0; i < this.obituaryMemberList.length; i++) {
        if (this.obituaryMemberList[i].obituaryMemberLeader) {
          if (this.obituaryMemberList[i].obituaryMemberPhone === null || this.obituaryMemberList[i].obituaryMemberPhone === undefined || this.obituaryMemberList[i].obituaryMemberPhone === '') {
            alert('대표상주의 전화번호를 입력해 주세요.')
            return false
          }
        }
      }

      
			for(let i=0;i<this.obituaryMemberList.length;i++){
				if(this.obituaryMemberList[i].obituaryMemberBank!=='' || this.obituaryMemberList[i].obituaryMemberBankNumber!==''){
					if(this.obituaryMemberList[i].success === true){

					}else{
						alert(this.obituaryMemberList[i].obituaryMemberName + '의 계좌 인증을 진행해 주세요.')
						return false
					}
				}
			}

      return true
    },
    mortuarySelectTypeChange(event) {
      if (this.memberInfo.memberAttr === 'TYPE-0') {
        if (event.target.value === 'in') {
          this.obituary.obituaryMortuary = { id: '' }
        } else {
          this.obituary.obituaryMortuary = null
        }
      } else {
        if (event.target.value === 'in') {
          this.obituary.obituaryMortuaryName = ''
        } else {
          this.obituary.obituaryMortuaryName = null
        }
      }
    },
    addrRegPopup() {
      $('.address_search').addClass('active');
			this.$store.commit('START_DEEMED');
    },
    registAddress(val) {
      this.obituary.obituaryAddrName = val.place
			this.obituary.obituaryAddr = val.address
			this.obituary.obituaryNumber = val.phone.replace(/-/gi,"")
			this.obituary.post = val.post
			
      this.obituary.obituaryAddrX = val.x
      this.obituary.obituaryAddrY = val.y
      this.obituary.obituaryFuneralAddr = val.memberFuneralAddr
    }
  }
}
</script>

